.server-security-container {
  position: absolute;
  top: 210px;
  right: 80px;
  width: 1600px;
  height: 640px;
  background: url(./images/bg.png) no-repeat center / 100% 100%;
}

.basic-info {
  display: flex;
  width: 92%;
  height: 110px;
  margin: 88px auto 0 auto;
  .item {
    height: 110px;
    box-sizing: border-box;
    padding: 12px 16px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
    .title {
      font-size: 26px;
      color: #FFF;
    }
    .value {
      margin-top: 14px;
      font-size: 48px;
      font-family: digifacewide;
      color: #FFF;
    }
  }
  .server-count {
    width: 406px;
    background-image: url(./images/pane-1.png);
  }
  .virus-count,
  .defeat-count,
  .attack-count,
  .dispose-count
   {
    width: 194px;
    margin-left: 56px;
    .title {
      color: #040506;
    }
    .value {
      cursor: pointer;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .virus-count {
    background-image: url(./images/pane-2.png);
  }
  .defeat-count {
    background-image: url(./images/pane-2.png);
  }
  .attack-count {
    background-image: url(./images/pane-3.png);
  }
  .dispose-count {
    background-image: url(./images/pane-3.png);
  }
}

.server-security-chart-container {
  width: 92%;
  height: 390px;
  margin: 50px auto 0 auto;
  .title {
    position: relative;
    width: 100%;
    height: 50px;
    color: #36ebff;
    font-size: 32px;
    &:after {
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      content: '';
      width: 100%;
      height: 10px;
      background: url(./images/title-bg.png) no-repeat center / 100% 100%;
    }
  }
  .chart {
    width: 100%;
    height: 280px;
    margin-top: 18px;
  }
}



