header {
  position: relative;
  width: 100%;
  height: 152px;
  h1 {
    position: absolute;
    left: 40px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 60px;
    font-family: 锐字锐线怒放黑简;
    color: #FFF;
  }
  h2 {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 50px;
    font-family: 锐字锐线怒放黑简;
    color: #FFF;
    transform: translate(-50%, -50%);
  }
  .user-box {
    font-size: 26px;
    position: absolute;
    right: 56px;
    bottom: 10px;
    color: #134ded;
    span {
      display: inline-block;
      width: 200px;
      height: 80px;
      line-height: 80px;
      text-align: center;
      cursor: pointer;
      .icon {
        display: inline-block;
        margin-right: 10px;
        width: 55px;
        height: 55px;
        background: url(./images/icon.png) no-repeat;
        vertical-align: middle;
      }
    }
    .user-name {
      color: #35ecfa;
    }
    .log-out {
      .icon {
        background-position: 0 -56px;
      }
    }
  }
}



