.side-l-nav {
  width: 36px;
  right: 0;
  top: 0;
  position: fixed;
  z-index: 999;
  overflow: hidden;
  height: 100%;

  .side-l-inner-wrap {
    opacity: 1;
    width: 100%;
    height: 100%;
    background: url(./images/nav-bg.png) no-repeat;
    background-size: 100% 100%;
    transform: translate(667px, 0);
    transition: all 1s; 

    ul {
      position: absolute;
      top: 50%;
      left: 206px;
      margin-top: -40%;
      width: 100%;

      .icon {
        display: inline-block;
        width: 78px;
        height: 78px;
        background: url(./images/icon.png) no-repeat;
        background-position: -84px -84px;
        vertical-align: middle;
      }
      .value {
        padding-left: 60px; 
        display: inline-block;
        flex-grow: 1;
        height: 137px;
        line-height: 137px;
        width: 400px;
        font-size: 36px;
        color: #82a2ea;
        background: url(./images/nav.png) no-repeat;
        background-position: -414px 0;
        vertical-align: middle;
      }
      li:nth-child(1) {
        .icon {
          background-position: -84px 0;
        }
      }
      li:nth-child(2) {
        .value {
          background-position: -414px -136px;
        }
      }
      li:nth-child(3) {
        .value {
          background-position: -414px -270px;
        }
      }
      li:nth-child(4) {
        .value {
          background-position: -414px -405px;
        }
      }
      li:nth-child(5) {
        .value {
          background-position: -414px -539px;
        }
      }
      /* 导航hover效果 */
      li:hover,
      li.active{
        .icon {
          background-position-x: 0;
        }
        .value {
          background-position-x: 0;
          font-size: 40px;
          color: #fff;
        }
      }

    }
  }
}

.side-l-nav:hover{
  & {
    width: 667px;
  }
  .side-l-inner-wrap {
    opacity: 1;
    transform: translate(0, 0);
  }
}


