.modal-container {
    position: fixed;
    z-index: 9999;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .54);
    .modal-wrapper {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10000;
        transform: translate(-50%, -50%);
        width: 2648px;
        height: 1112px;
        background: url(./images/bg.png) no-repeat center / 100% 100%;
        .btn-close {
            cursor: pointer;
            position: absolute;
            top: 16px;
            right: 32px;
            width: 80px;
            height: 80px;
            background: url(./images/icon-close.png) no-repeat center / 100% 100%;
        }
        .divider {
            position: absolute;
            top: 160px;
            left: 50%;
            transform: translateX(-50%);
            width: 92%;
            height: 4px;
            background: url(./images/divider-1.png) no-repeat center / 100% 100%;
        }
        table {
            width: 92%;
            margin: 84px auto 0 auto;
            .header {
                font-size: 38px;
                color: #FFF;
                th {
                    padding-bottom: 54px;
                }
            }
            .row {
                font-size: 32px;
                color: #36ebff;
                &:hover {
                    background: url(./images/emphasis.png) no-repeat center / 100% 100%;
                }
                th {
                    padding: 32px 0;
                }
            }
        }
        .pagenation-group {
            position: absolute;
            left: 50%;
            bottom: 60px;
            transform: translateX(-50%);
            display: flex;
            justify-content: flex-end;
            width: 92%;
            .pagenation {
                cursor: pointer;
                width: 220px;
                height: 78px;
                line-height: 78px;
                background: url(./images/btn-bg.png) no-repeat center / 100% 100%;
                font-size: 30px;
                color: #36ebff;
                text-align: center;
                text-shadow: 0 0 15px #36ebff;
                &.next {
                    margin-left: 32px;
                }
            }
        }
    }
}



