.internet-container {
    position: absolute;
    bottom: 50px;
    left: 80px;
    width: 1568px;
    height: 1630px;
    background: url(./images/bg.png) no-repeat center / 100% 100%;
}

.network-statistics-container {
    display: flex;
    width: 92%;
    margin: 0 auto;
    padding-top: 72px;
    .statistics-item {
        display: flex;
        align-items: center;
        width: 568px;
        height: 192px;
        .icon {
            position: relative;
            width: 192px;
            height: 192px;
            span {
                display: block;
                position: absolute;
                background-repeat: no-repeat;
                background-position: center;
                background-size: 100% 100%;
            }
            span:nth-child(1) {
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                animation: rotate 4s linear infinite;
            }
            span:nth-child(2) {
                width: 50px;
                height: 48px;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
        &.flow .label {
            width: 360px;
        }
        &.flow .icon {
            span:nth-child(1) {
                background-image: url(./images/circle-1.png);
            }
            span:nth-child(2) {
                background-image: url(./images/icon-1.png);
            }
        }
        &.attack .icon {
            span:nth-child(1) {
                background-image: url(./images/circle-2.png);
            }
            span:nth-child(2) {
                background-image: url(./images/icon-2.png);
            }
        }
        &.intercept .icon {
            span:nth-child(1) {
                background-image: url(./images/circle-3.png);
            }
            span:nth-child(2) {
                background-image: url(./images/icon-3.png);
            }
        }
        .label {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 266px;
            height: 84px;
            background: url(./images/label-bg.png) no-repeat center / 100% 100%;
            span {
                display: block;
            }
            span:nth-child(1) {
                font-size: 28px;
                color: #a4d5ff
            }
            span:nth-child(2) {
                margin-left: 16px;
                font-size: 40px;
                color: #FFF;
            }
        }
    }
}

.feature-container {
    .feature-wrapper {
        position: relative;
        width: 1164px;
        height: 1164px;
        margin: -20px auto 0 auto;
        background: url(./images/feature-bg.png) no-repeat center / 100% 100%;
        .radar {
            width: 100%;
            height: 100%;
            background: url(./images/radar.png) no-repeat center / 100% 100%;
            animation: rotate 2s;
        }
        .label {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -30%);
            text-align: center;
            .title {
                font-size: 44px;
                font-family: 锐字锐线怒放黑简;
                color: #FFF;
            }
            .count {
                font-size: 32px;
                color: #ffd200;
                span {
                    font-size: 62px;
                    font-family: digifacewide;
                }
            }
        }
        .statistics {
            position: absolute;
            top: 680px;
            left: 50%;
            text-align: center;
            transform: translateX(-50%);
            width: 458px;
            height: 110px;
            padding: 24px;
            box-sizing: border-box;
            /* background: url(./images/radar-label-bg.png) no-repeat center / 100% 100%; */
            font-size: 22px;
            color: #ffd200;
            span {
                color: #35ebff;
            }
        }
        .feature-group {
            .feature-library {
                position: absolute;
                width: 280px;
                height: 280px;
                span {
                    display: block;
                    position: absolute;
                }
                span.bg {
                    left: 0;
                    top: 0;
                    width: 280px;
                    height: 280px;
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: 100% 100%;
                }
                span.status-0 {
                    background-image: url(./images/feature-normal.png);
                }
                span.status-1 {
                    animation: featureEmphasis 2s ease forwards;
                }
                span.text {
                    width: 130px;
                    max-height: 130px;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    font-size: 26px;
                    text-align: center;
                    color: #a4d5ff;
                }
            }
            .feature-library:nth-child(1) {
                top: 0;
                left: 50%;
                transform: translateX(-50%);
            }
            .feature-library:nth-child(2) {
                top: 8%;
                right: 15%;
            }
            .feature-library:nth-child(3) {
                top: 26%;
                right: 2%;
            }
            .feature-library:nth-child(4) {
                top: 50%;
                right: 2%;
            }
            .feature-library:nth-child(5) {
                top: 68%;
                right: 15%;
            }
            .feature-library:nth-child(6) {
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
            }
            .feature-library:nth-child(7) {
                top: 68%;
                left: 15%;
            }
            .feature-library:nth-child(8) {
                top: 50%;
                left: 2%;
            }
            .feature-library:nth-child(9) {
                top: 26%;
                left: 2%;
            }
            .feature-library:nth-child(10) {
                top: 8%;
                left: 15%;
            }
        }
    }
    .feature-table {
        width: 92%;
        margin: 32px auto 0 auto;
        .header {
            display: flex;
            .th {
                width: 16.6666%;
                font-size: 32px;
                color: #FFF;
                text-align: center;
            }
        }
        .row-container {
            position: relative;
            width: 100%;
            height: 80px;
            margin-top: 16px;
            overflow: hidden;
            background: url(./images/table-row-pane.png) no-repeat center / 100% 100%;
            .row {
                display: flex;
                align-items: center;
                width: 100%;
                .th {
                    width: 16.6666%;
                    height: 80px;
                    line-height: 80px;
                    font-size: 26px;
                    color: #36ebff;
                    text-align: center;
                }
            }
        }
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg)
    }
    to {
        transform: rotate(359deg)
    }
}

@keyframes featureEmphasis {
    0% {
        background-image: url(./images/feature-normal.png);
    }
    20% {
        background-image: url(./images/feature-emphasis.png);
    }
    40% {
        background-image: url(./images/feature-normal.png);
    }
    60% {
        background-image: url(./images/feature-emphasis.png);
    }
    80% {
        background-image: url(./images/feature-normal.png);
    }
    100% {
        background-image: url(./images/feature-emphasis.png);
    }
}



