body {
  background: url(./images/bg.jpg) no-repeat center / 100% 100%;
}

main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
}

.com-hint {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -15px;
  font-size: 30px;
  color: #fff;
}

/* 浮窗 */
.com-floating {
  display: none;
  position: absolute;
  top: 0px;
  left: 0px;
  padding: 10px 20px 30px;
  min-width: 300px;
  max-width: 400px;
  font-size: 26px;
  line-height: 40px;
  color: #fff;
  min-height: 60px;
  background: url(./images/frame-bg.png);
  border-radius: 20px;
  border: 2px solid #0226dd;
  z-index: 10;
  .img-top {
    position: relative;
    top: -18px;
    width: 100%;
    height: 20px;
    background: url(./images/frame-top.png) no-repeat center;
    &::before,
    &::after {
        position: absolute;
        left: -22px;
        top: -2px;
        content: url(./images/frame-bor.png);
    }
    &::after {
        left: auto;
        right: -22px;
        transform: rotate(90deg);
    }
  }
  &::before,
  &::after {
      position: absolute;
      left: 0;
      bottom: -9px;
      content: url(./images/frame-bor.png);
      transform: rotate(270deg);
  }
  &::after {
      left: auto;
      right: -4px;
      transform: rotate(180deg);
  }
}


