.border-flow-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: absolute;
  bottom: 34px;
  right: 80px;
  width: 1600px;
  height: 990px;
  padding: 70px 34px 0 34px;
  box-sizing: border-box;
  background: url(./images/bg.png) no-repeat center / 100% 100%;

  .bar-chart {
    width: 544px;
    height: 502px;
    background: url(./images/bar-chart-bg.png) no-repeat center / 100% 100%;
  }

  .flow-top5-container {
    width: 930px;
    height: 502px;
    .title {
      display: flex;
      justify-content: space-between;
      position: relative;
      width: 100%;
      height: 52px;
      &:after {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        content: '';
        width: 100%;
        height: 10px;
        background: url(./images/title-bg.png) no-repeat center / cover;
      }
      span {
        font-size: 32px;
        color: #36ebff;
        text-shadow: 0 0 30px #36ebff;
      }
      select {
        width: 172px;
        height: 46px;
        background: url(./images/selector-bg.png) no-repeat center / 100% 100%;
        border: none;
        outline: none;
        font-size: 26px;
        color: #FFF;
        option {
          background: #19395b;
          font-size: 14px;
        }
      }
    }

    table {
      table-layout: fixed;
      width: 100%;
      height: auto;
      background: url(./images/table-bg.png) no-repeat bottom / 100% 87%;
      .header {
        font-size: 26px;
        color: #FFF;
        th {
          padding: 16px 0;
        }
        th:nth-child(4){
          width: 280px;
        }
      }
      .row {
        width: 100%;
        height: 78px;
        line-height: 78px;
        font-size: 26px;
        color: #36ebff;
        &:hover {
          background: url(./images/list-emphasis.png) no-repeat center / 100% 100%;
        }
        th:nth-child(1){
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        th {
          border-bottom: 1px solid #011757;
        }
        th:nth-child(3) {
          color: #edc506;
        }
      }
    }
  }

  .line-chart {
    width: 100%;
    height: 360px;
    .title {
      position: relative;
      width: 100%;
      height: 52px;
      font-size: 32px;
      color: #36ebff;
      text-shadow: 0 0 30px #36ebff;
      &:after {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        content: '';
        width: 100%;
        height: 10px;
        background: url(./images/title-bg.png) no-repeat center / 100% 100%;
      }
    }
    .chart {
      width: 100%;
      height: 282px;
      margin-top: 16px;
    }
  }
}



